import { renameKeysFromRESTResponseToSDKResponse } from '@wix/sdk-runtime/rename-all-nested-keys';
import { transformRESTTimestampToSDKTimestamp } from '@wix/sdk-runtime/transformations/timestamp';
import { transformPaths } from '@wix/sdk-runtime/transformations/transform-paths';
import { EventDefinition, HttpClient, NonNullablePaths } from '@wix/sdk-types';
import {
  GenerateOAuthAppSecretResponse,
  OAuthApp,
  OAuthAppCreatedEnvelope,
  OAuthAppDeletedEnvelope,
  OAuthAppUpdatedEnvelope,
  OAuthAppsQueryBuilder,
  UpdateOAuthApp,
  createOAuthApp as universalCreateOAuthApp,
  deleteOAuthApp as universalDeleteOAuthApp,
  generateOAuthAppSecret as universalGenerateOAuthAppSecret,
  getOAuthApp as universalGetOAuthApp,
  queryOAuthApps as universalQueryOAuthApps,
  updateOAuthApp as universalUpdateOAuthApp,
} from './headless-v1-o-auth-app-o-auth-apps.universal.js';

export const __metadata = { PACKAGE_NAME: '@wix/auth-management' };

export function createOAuthApp(
  httpClient: HttpClient
): CreateOAuthAppSignature {
  return (oAuthApp: NonNullablePaths<OAuthApp, `name`, 2>) =>
    universalCreateOAuthApp(
      oAuthApp,
      // @ts-ignore
      { httpClient }
    );
}

interface CreateOAuthAppSignature {
  /**
   * Creates a new OAuth app for a Wix Headless client.
   *
   * Use this API to enable headless functionality for a Wix site. An OAuth app allows external sites or apps to securely access your site's data and services using Wix APIs.
   * @param - OAuth app to create.
   * @returns Created OAuth app info.
   */
  (oAuthApp: NonNullablePaths<OAuthApp, `name`, 2>): Promise<
    NonNullablePaths<
      OAuthApp,
      | `allowedRedirectUris`
      | `allowedRedirectDomains`
      | `applicationType`
      | `technology`
      | `wixPagesDomainsMappings`
      | `wixPagesDomainsMappings.${number}.origin`
      | `wixPagesDomainsMappings.${number}.destination`,
      4
    >
  >;
}

export function getOAuthApp(httpClient: HttpClient): GetOAuthAppSignature {
  return (oAuthAppId: string) =>
    universalGetOAuthApp(
      oAuthAppId,
      // @ts-ignore
      { httpClient }
    );
}

interface GetOAuthAppSignature {
  /**
   * Retrieves an OAuth app by ID.
   * @param - ID of the OAuth app to retrieve.
   * @returns Retrieved OAuth app info.
   */
  (oAuthAppId: string): Promise<
    NonNullablePaths<
      OAuthApp,
      | `allowedRedirectUris`
      | `allowedRedirectDomains`
      | `applicationType`
      | `technology`
      | `wixPagesDomainsMappings`
      | `wixPagesDomainsMappings.${number}.origin`
      | `wixPagesDomainsMappings.${number}.destination`,
      4
    >
  >;
}

export function updateOAuthApp(
  httpClient: HttpClient
): UpdateOAuthAppSignature {
  return (_id: string, oAuthApp: UpdateOAuthApp, mask: string[]) =>
    universalUpdateOAuthApp(
      _id,
      oAuthApp,
      mask,
      // @ts-ignore
      { httpClient }
    );
}

interface UpdateOAuthAppSignature {
  /**
   * Updates an OAuth app.
   *
   *
   * Only fields provided in `mask.paths` are updated.
   *
   * You can update the following fields:
   * + `name`
   * + `description`
   * + `allowedDomain`
   * + `loginUrl`
   * + `logoutUrl`
   * + `technology`
   * @param - ID of the OAuth app.
   * @param - Explicit list of fields to update. Only fields listed are updated.
   * @returns Updated OAuth app info.
   */
  (_id: string, oAuthApp: UpdateOAuthApp, mask: string[]): Promise<
    NonNullablePaths<
      OAuthApp,
      | `allowedRedirectUris`
      | `allowedRedirectDomains`
      | `applicationType`
      | `technology`
      | `wixPagesDomainsMappings`
      | `wixPagesDomainsMappings.${number}.origin`
      | `wixPagesDomainsMappings.${number}.destination`,
      4
    >
  >;
}

/** @internal */
export function deleteOAuthApp(
  httpClient: HttpClient
): DeleteOAuthAppSignature {
  return (oAuthAppId: string) =>
    universalDeleteOAuthApp(
      oAuthAppId,
      // @ts-ignore
      { httpClient }
    );
}

interface DeleteOAuthAppSignature {
  /**
   * Deletes an OAuth app by ID.
   *
   *
   * > **Note:** After you delete an OAuth app, an external client can no longer make API calls by authenticating with its client ID.
   * @param - ID of the OAuth app to delete.
   */
  (oAuthAppId: string): Promise<void>;
}

export function queryOAuthApps(
  httpClient: HttpClient
): QueryOAuthAppsSignature {
  return () =>
    universalQueryOAuthApps(
      // @ts-ignore
      { httpClient }
    );
}

interface QueryOAuthAppsSignature {
  /**
   * Retrieves a list of OAuth apps, given the provided paging, filtering, and sorting.
   *
   *
   * Query OAuth Apps runs with these defaults, which you can override:
   * + Results are sorted by `id` in descending order.
   * + `paging.offset` is `0`.
   */
  (): OAuthAppsQueryBuilder;
}

/** @internal */
export function generateOAuthAppSecret(
  httpClient: HttpClient
): GenerateOAuthAppSecretSignature {
  return (oAuthAppId: string) =>
    universalGenerateOAuthAppSecret(
      oAuthAppId,
      // @ts-ignore
      { httpClient }
    );
}

interface GenerateOAuthAppSecretSignature {
  /**
   * Generates a secret for an existing OAuth app.
   *
   *
   * > **Note:** You can only generate a secret once for each OAuth app, and the secret can't be retrieved later. Store the secret securely.
   * @param - ID of the OAuth app to generate a secret for.
   */
  (oAuthAppId: string): Promise<
    NonNullablePaths<GenerateOAuthAppSecretResponse, `oAuthAppSecret`, 2>
  >;
}

export const onOAuthAppCreated = EventDefinition(
  'wix.headless.v1.o_auth_app_created',
  true,
  (event: OAuthAppCreatedEnvelope) =>
    renameKeysFromRESTResponseToSDKResponse(
      transformPaths(event, [
        {
          transformFn: transformRESTTimestampToSDKTimestamp,
          paths: [
            { path: 'entity.createdDate' },
            { path: 'metadata.eventTime' },
          ],
        },
      ])
    )
)<OAuthAppCreatedEnvelope>();
export const onOAuthAppDeleted = EventDefinition(
  'wix.headless.v1.o_auth_app_deleted',
  true,
  (event: OAuthAppDeletedEnvelope) =>
    renameKeysFromRESTResponseToSDKResponse(
      transformPaths(event, [
        {
          transformFn: transformRESTTimestampToSDKTimestamp,
          paths: [
            { path: 'undefined.createdDate' },
            { path: 'metadata.eventTime' },
          ],
        },
      ])
    )
)<OAuthAppDeletedEnvelope>();
export const onOAuthAppUpdated = EventDefinition(
  'wix.headless.v1.o_auth_app_updated',
  true,
  (event: OAuthAppUpdatedEnvelope) =>
    renameKeysFromRESTResponseToSDKResponse(
      transformPaths(event, [
        {
          transformFn: transformRESTTimestampToSDKTimestamp,
          paths: [
            { path: 'entity.createdDate' },
            { path: 'metadata.eventTime' },
          ],
        },
      ])
    )
)<OAuthAppUpdatedEnvelope>();

